import Vue from 'vue';
import Vuex from 'vuex'
import App from './App.vue';
import router from './router';
import store from './store';
import axios from 'axios';
import { getPersonalInfo } from '@/api';
Vue.prototype.$axios = axios;

Vue.config.productionTip = false;

import 'mescroll.js/mescroll.min.css';

import {
  Lazyload,
  Toast,
  Dialog,
  Panel ,
  Icon,
  Button,
  Cell,
  CellGroup,
  Sidebar,
  SidebarItem,
  Search,
  Form,
  Field
} from 'vant';
// options 为可选参数，无则不传
Vue.use(Vuex);
Vue.use(Lazyload);
Vue.use(Toast);
Vue.use(Dialog);
Vue.use(Panel);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Icon);
Vue.use(Button);
Vue.use(Sidebar);
Vue.use(SidebarItem);
Vue.use(Search);
Vue.use(Form);
Vue.use(Field);

//vue-video-player
import 'video.js/dist/video-js.css';
import 'vue-video-player/src/custom-theme.css';
// import 'videojs-contrib-hls'
import VideoPlayer from 'vue-video-player';
Vue.use(VideoPlayer);

import util from '@/utils/util';
Vue.prototype.$utils = util;

import config from '@/utils/config';
Vue.prototype.$config = config;

//微信jsjdk
import wx from 'weixin-js-sdk';
Vue.prototype.$wx = wx;

//引入qs，用于转化post是formdata的问题
import qs from 'qs';
Vue.prototype.$qs = qs;

// require('@/utils/vconsole.js')

store.registerModule('vux', {
  state: {
    direction: 'forward'
  },
  mutations: {}
});
let that = this;
router.beforeEach(function (to, from, next) {
  // next();
  if (process.env.NODE_ENV === 'development') {
    next();
  } else {
    // console.log(to)
    // console.log(to.meta)
    // console.log(to.meta.requireAuth)

    if(to.meta.requireAuth){ // 如果下一个路由需要登录凭证

      if (!sessionStorage.getItem('token')) { // 所有登录凭证都没有，去登录页面

        // console.log(window.location.href.replace(config.http, ''))
        // let url = window.location.href.replace(config.http, '')
        //   .replace("?from=singlemessage&isappinstalled=0&scene=1&clicktime=1577428006&enterid=1577428006", "")
        //   .replace("?from=singlemessage&from=singlemessage", "")
        //   .replace("?from=singlemessage&isappinstalled=0", "")
        //   .replace("?from=timeline&isappinstalled=0", "")
        //   .replace("?from=groupmessage&isappinstalled=0", "")
        //   .replace("?from=singlemessage", "");
        // sessionStorage.setItem('url', url);
        // console.log('去的地址', url);
        let hash = location.hash;
        if(hash) sessionStorage.setItem('hash',hash.replace('#',''));
        

        if(to.name === 'myLogin')
          next();
        else
          next('myLogin');

        // return false;
      }else
        next();
    }else
      next();

    
  }
});


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');